.about {
  background-color: #fff5f6;
}

.about__title-wrapper {
  height: 112px;
  width: 200px;
  margin: auto;
  margin-top: 125px;
  border-radius: nullpx;
}

.about__label {
  color: #ffa5ba;
  font-family: Titan One;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0em;
  text-align: center;
}

.about__title {
  color: #d41443;
  font-family: Titan One;
  font-size: 34px;
  font-weight: 400;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-align: center;
}

.about__picture-wrapper {
  height: 279px;
  width: 280px;
  border-radius: 0px;
}

.about__paragraph-wrapper {
  display: flex;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: left;
}

.about__list {
  list-style-type: none;
  list-style-position: inside;
  padding: 0;
}

.about__statistics {
  list-style-type: none;
  list-style-position: inside;
  margin-bottom: 38px;
  padding-left: 0;
}

.about__list-item {
  margin: auto;
}

.about__list-item--background {
  width: 100vw;
  // background-image: url(/images/background.png);
  background-repeat: no-repeat;
  background-size: 100vw;
}

.about__paragraph {
  width: 220px;
  color: #000000;
  margin: auto;
}

.about__paragraph--background {
  width: 220px;
  color: #907e82;
  margin: auto;
  margin-bottom: 17px;
  margin-top: 47px;
}

.about__button {
  margin: auto;
  height: 44px;
  width: 176px;
  border: none;
  padding: 0;
  background-color: #fff5f6;
  &:hover {
    cursor: pointer;
  }
}

.button__wrapper {
  display: flex;
  justify-content: center;
}

.about__statistics-number {
  font-family: Titan One;
  font-size: 36px;
  font-weight: 400;
  line-height: 41px;
  letter-spacing: 0.04em;
  text-align: left;
  color: #d41443;
}

.about__statistics-label {
  width: 230px;
  height: 36px;
  margin-top: 0;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
  color: #000000;
}

.about__statistics-item {
  padding-left: 21px;
  padding-bottom: 16px;
}
